<template>
  <div class="cart-drawer critical-component-hide">
    <div class="container">
      <div
        ref="header"
        class="cart-drawer__title h4"
        v-text="$string('cart.title')"
      />

      <cart-bundle
        v-if="cartBundles && cartBundles?.length > 0"
        :bundles="cartBundles"
        :fromMainCart="false"
        @check-cart-bundle="cartBundles"
      />

      <form
        v-if="cart.item_count > 0 || cartStatus.label === 'adding'"
        class="cart-drawer__body"
        :action="$variable('routes.cart.url')"
        method="post"
      >
        <div
          class="cart-drawer__products"
          js-overlay="scrollElement"
        >
          <line-item
            v-for="(item, index) in cartItems"
            :key="getCartItemKey(item, index)"
            block-class="cart-drawer"
            :index="index"
            :item="item"
            :bundle-discount="bundleDiscount(item)"
          />
        </div>

        <div
          ref="footer"
          class="cart-drawer__footer"
        >
          <p
            class="cart-drawer__subtitle"
            :class="{ [$class('loading')]: cart.item_count === 0 }"
          >
            <span
              class="cart-drawer__items h6"
              v-text="`${$string('cart.general.subtotal')}:`"
            />

            <span
              class="cart-drawer__price h5"
              v-text="$formatMoney(cart.total_price)"
            />
          </p>

          <div class="cart-drawer__actions">
            <btn
              class="cart-drawer__view-cart"
              modifiers="secondary"
              :label="$string('cart.view_cart')"
              :url="$variable('routes.cart.url')"
            />

            <btn
              class="cart-drawer__checkout"
              modifiers="primary"
              name="checkout"
              :label="$string('cart.checkout')"
              type="submit"
            />
          </div>

          <payment-icons :payments="payments" />

          <div class="cart-drawer__disclaimer body-2">
            <span
              v-if="disclaimer"
              class="cart-drawer__disclaimer-text body-2"
              v-html="disclaimer"
            />
            <template v-else>
              <span
                class="cart-drawer__disclaimer-text body-2"
                v-text="$string('cart.general.shipping_at_checkout')"
              />
              <span
                class="cart-drawer__disclaimer-text body-2"
                v-text="$string(
                  'cart.general.currency_disclaimer',
                  { replace: { currency: $variable('store.baseCurrency') } }
                )"
              />
            </template>
          </div>

          <div
            v-if="partnerFeature"
            class="cart-drawer__partner-feature"
          >
            <responsive-image
              v-if="partnerFeature.image"
              class="cart-drawer__partner-feature-image"
              :alt="partnerFeature.image.alt"
              :image="{src: partnerFeature.image.src}"
              min-max="73-120"
            />

            <p
              v-if="partnerFeature.caption"
              class="cart-drawer__partner-feature-caption body-2"
              v-text="partnerFeature.caption"
            />
          </div>
        </div>
      </form>

      <div
        v-else
        class="cart-drawer__empty"
      >
        <p
          class="cart-drawer__empty-text text-body-m"
          v-text="$string('cart.empty.text')"
        />
      </div>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Cart drawer
 * -----------------------------------------------------------------------------
 * Displays contents of cart drawer.
 *
 * @param {Boolean} isActive - Overlay drawer is active.
 *
 */
import { nextTick } from 'vue'
import { mapActions, mapState } from 'vuex'
import { debounce } from '~/helpers/general'

import ResponsiveImage from '~global/images/responsive-image'
import Btn from '~global/btn/btn'
import CartBundle from '~async/cart-bundle/cart-bundle'
import LineItem from '~async/line-item/line-item'
import PaymentIcons from '~async/payment-icons/payment-icons'

export default {
  name: 'CartDrawer',

  components: {
    ResponsiveImage,
    Btn,
    CartBundle,
    LineItem,
    PaymentIcons,
  },

  props: {
    isActive: {
      type: Boolean,
      default: false,
    },
    payments: {
      type: Object,
      default: () => ({}),
    },
    disclaimer: {
      type: String,
      default: '',
    },
    partnerFeature: {
      type: [Object, Boolean],
      default: false,
    },
  },

  data() {
    return {
      bundles: this.$variable('productBundles'),
      matchingBundles: [],
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      cart: (state) => state.cart.response,
      cartStatus: (state) => state.cart.status,
      settings: (state) => state.index.settings,
    }),

    /**
     * Compute cart items.
     * - For each item that is being added, check if it's already in the cart.
     * - Check against key (if adding from cart) or id (if adding from other).
     * - If it isn't then add a false item to act as a loading placeholder.
     * @returns {Array}
     */
    cartItems() {
      if (this.cartStatus.label !== 'adding') {
        return this.cart.items
      }

      const placeholders = []

      this.cartStatus.items.forEach((addedItem) => {
        const match = this.cart.items.find((cartItem) => {
          const cartItemProperty = typeof addedItem === 'string'
            ? cartItem.key
            : cartItem.id

          return cartItemProperty === addedItem
        })

        if (match) {
          return
        }

        placeholders.push(false)
      })

      return [
        ...placeholders,
        ...this.cart.items,
      ]
    },

    /**
     * Computes header items string.
     * @returns {String}
     */
    itemsString() {
      const string = this.$string(
        'cart.items',
        { pluralise: this.cart.item_count },
      )

      return `${this.cart.item_count} ${string}:`
    },

    /**
     * Computes cart bundles
     * @returns {Object}
     */
    cartBundles() {
      if (!this.bundles) {
        return this.matchingBundles
      }

      this.bundles.items.forEach((bundle) => {
        const matchingBundle = this.cartItems.filter((item) => {
          return (bundle.bundleType === 'category' && item.product_type === bundle.bundleCategory)
          || (bundle.bundleType === 'single' && item.product_id === bundle.parentProductId)
        })

        if (matchingBundle.length > 0) {
          this.setMatchingBundle([bundle])
        }
      })

      return this.matchingBundles
    },
  },

  watch: {

    /**
     * Watch cart items to update focus trap.
     */
    'cart.items': function() {
      this.updateFocus()
    },
  },

  mounted() {
    this.updateFocus()
    this.handleResize()

    window.visualViewport.addEventListener(
      'resize',
      debounce(() => this.handleResize(), this.settings.throttle),
    )
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      closeOverlay: 'overlays/close',
      openOverlay: 'overlays/open',
    }),

    /**
     * Get cart item key.
     * @param {Object} item - Line item.
     * @param {Number} index - Line item index.
     * @returns {String}
     */
    getCartItemKey(item, index) {
      if (!item) {
        return `cart-line-item-loading-${index}`
      }

      return `cart-line-item-${item.key}`
    },

    /**
     * Update focus trap and scroll lock.
     */
    updateFocus() {
      nextTick(() => {
        cnvs.EventBus.emit('overlay:update', 'cartDrawer')
        this.handleResize()
      })
    },

    /**
     * Shopify section editor select event.
     */
    shopifySectionSelect() {
      this.openOverlay({
        component: 'cart-drawer',
        namespace: 'cartDrawer',
        ignoreDismissed: true,
      })
    },

    /**
     * Shopify section editor deselect event.
     */
    shopifySectionDeselect() {
      this.closeOverlay('cartDrawer')
    },

    handleResize() {
      document.documentElement.style.setProperty(
        '--cart-drawer-header-height',
        `${this.$refs.header.offsetHeight}px`,
      )

      if (!this.$refs.footer) {
        return
      }

      document.documentElement.style.setProperty(
        '--cart-drawer-footer-height',
        `${this.$refs.footer.offsetHeight}px`,
      )
    },

    filterMatchingBundles() {
      this.matchingBundles = this.matchingBundles.filter(bundle => {
        return !this.cartItems.find(cartItem => cartItem.product_id === bundle.id)
      })
    },

    /**
     * Set matching bundle
     * @param {Object} matchingBundle - Matching bundle.
     */
    setMatchingBundle(matchingBundle) {
      matchingBundle.forEach((bundle) => {
        bundle.discountedProducts.forEach((discountProduct) => {
          if (!discountProduct.discountedProduct.available) {
            return
          }

          const hasBundle = this.matchingBundles.find((item) => item.id === discountProduct.discountedProduct.id)

          if (hasBundle) {
            return
          }

          this.matchingBundles.push({
            ...discountProduct.discountedProduct,
            swatchUrls: discountProduct.swatchUrls,
            discountAmount: bundle.discountAmount,
            discountType: bundle.discountType,
            discountAmountSaved: bundle.discountAmountSaved,
          })
        })
      })
      this.filterMatchingBundles()
    },

    /**
     * Get bundle discount percent
     * @param {Object} item - Line item
     * @return {Number}
     */
    bundleDiscount(item) {
      return item.properties?._buyxgety || false
    },
  },
}
</script>

<style lang="scss">
@import './cart-drawer';
</style>
